.section__home {
  width: 100%;
  height: 100vh;
}

#container {
  width: 100vw;
  height: 100vh;
}

.home__title {
  margin: 0;
  font-size: var(--font-title);
  letter-spacing: var(--letter-space);
  font-weight: 400;
  height: fit-content;
  width: fit-content;

  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);

  color: var(--color-white);
  font-family: "Libre Baskerville", serif;
  overflow: hidden;
  /* border: thin solid red; */
}

.home__title > div {
  /* border: thin solid blue; */

  opacity: 0;
  will-change: transform;
}

