nav {
  padding: 0 var(--margin-h);
  letter-spacing: 2px;
  font-weight: 500;
  font-size: var(--font-small);
  position: fixed;
  width: 100%;
  display: flex;
  align-items: center;
  justify-content: space-between;
  color: var(--color-bg);
  bottom: 2rem;
  mix-blend-mode: difference;
  z-index: 10;
}

.menu {
  color: var(--color-white);
  position: relative;
  text-decoration: none;
  /* border: thin solid red; */
  overflow: hidden;
}
.menu > .open {
  /* border: thin solid blue; */
  will-change: transform;
  padding: .25rem 0;

}

.menu > .close {
  /* border: thin solid blue; */
  padding: .25rem 0;
  position: absolute;
  top: 0;
  left: 0;
  transform: translateY(100%);
  will-change: transform;
}

/*  */

.sidebar {
  width: 100%;
  height: 100%;
  position: fixed;
  z-index: 10;
  right: 0;
  top: 0;
  /* transition: 300ms ease-in-out; */
  opacity: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 1rem;
}

.sidebar > a {
  text-decoration: none;
  color: var(--color-dark);
  font-size: var(--font-subtitle);
  letter-spacing: 2px;
  font-weight: 400;
  overflow: hidden;
  line-height: 1;
  /* border: thin solid red; */
  padding: 0.5rem 0;
}

.sidebar > a > span {
  display: block;
  /* border: thin solid blue; */
  opacity: 0;
  /* transform: translateY(101%); */
}
