@import url("/src/style/home.css");
@import url("/src/style/work.css");
@import url("/src/style/about.css");
@import url("/src/style/overlay.css");
@import url("/src/style/preview.css");

@font-face {
  font-family: Libre Baskerville;
  src: url("../assets/LibreBaskerville.ttf");
}

:root {
  /* @link https://utopia.fyi/type/calculator/ */
  --step--2: clamp(0.91rem, calc(0.89rem + 0.10vw), 0.96rem);
  --step--1: clamp(1.09rem, calc(1.05rem + 0.21vw), 1.20rem);
  --step-0: clamp(1.31rem, calc(1.24rem + 0.37vw), 1.50rem);
  --step-1: clamp(1.58rem, calc(1.46rem + 0.59vw), 1.88rem);
  --step-2: clamp(1.89rem, calc(1.71rem + 0.89vw), 2.34rem);
  --step-3: clamp(2.27rem, calc(2.01rem + 1.29vw), 2.93rem);
  --step-4: clamp(2.72rem, calc(2.36rem + 1.83vw), 3.66rem);
  --step-5: clamp(3.27rem, calc(2.75rem + 2.56vw), 4.58rem);
  --step-6: clamp(3.92rem, calc(3.22rem + 3.52vw), 5.72rem);
  --step-7: clamp(4.70rem, calc(3.75rem + 4.78vw), 7.15rem);
  --step-8: clamp(5.64rem, calc(4.36rem + 6.43vw), 8.94rem);
  /* fluid font */
  --margin-h: clamp(1.2rem, calc(0.2rem + 5vw), 3.66rem);

  /* COLORS */
  --color-dark: #363637;
  --color-black: #000000;
  --color-white: #ffffff;
  --color-bg: #F2F2F2;
  --color-blue: #4e97f0;

  /* SIZES */
  --font-small: 0.75rem;
  --font-text: 1.25rem;
  --font-subtitle: clamp(1.58rem, calc(1.46rem + 0.59vw), 1.88rem);
  --font-title: calc(clamp(1.5rem, calc(1.5rem + 3.56vw), 6.58rem) * 0.85);
  --font-title-span: clamp(1.5rem, calc(1.5rem + 3.56vw), 6.58rem);

  --letter-space: clamp(0rem, calc(0rem + 5vw), 5rem);
}

html {
  box-sizing: border-box;
  margin: 0;
  scroll-behavior: smooth;
}
*,
*:before,
*:after {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
}

body {
  background-color: #000000;
  width: 100%;
  /* margin: 0 var(--margin-h); */
  font-family: 'Work Sans', sans-serif;
}


/*  */

.title-font-2 {
  font-family: "Work Sans", sans-serif;
  font-size: var(--font-title-span);
}
.subtitle{
  font-size: var(--font-subtitle);
  font-weight: 400;

}

.minititle{
  letter-spacing: 2px;
    font-weight: 500;
    font-size: var(--font-small);
}

.section {
  width: 100%;
  height: fit-content;
  /* height: 100vh; */
  padding: 8rem var(--margin-h);
}

/* open sidebar */
.open-sidebar{
  width: 100%;
}

/* LINK VISIBILITY */
a:focus {
	/* Provide a fallback style for browsers
	 that don't support :focus-visible */
	outline: none;
	background: lightgrey;
}
a:focus:not(:focus-visible) {
	/* Remove the focus indicator on mouse-focus for browsers
	 that do support :focus-visible */
	background: transparent;
}
a:focus-visible {
	/* Draw a very noticeable focus style for
	 keyboard-focus on browsers that do support
	 :focus-visible */
	outline: 2px solid red;
	background: transparent;
}





/* C U R S O R */
.cursor {
	display: none;
}

@media (any-pointer:fine) {
	.cursor {
    opacity: 0;
		position: fixed;
		top: 0;
		left: 0;
		display: block;
		pointer-events: none;
		z-index: 9999;
    mix-blend-mode: difference;
    border: 8px solid rgb(100, 100, 100);
    border-radius: 50%;
    user-select: none;
	}

	.cursor__inner {
		fill: none;
		stroke: var(--color-white);
		stroke-width: 2px;
	}

}


/* Loader */
.js .loading::before,
.js .loading::after {
	content: '';
	position: fixed;
	z-index: 1000;
}

.js .loading::before {
	top: 0;
	left: 0;
	width: 100%;
	height: 100%;
	background: var(--color-black);
  opacity: 1;
  transition: opacity 0.45s ease-in-out;
}

.js .loading::after {
	top: 50%;
	left: 50%;
	width: 100px;
	height: 100px;
	margin: -50px 0 0 -50px;
	border-radius: 50%;
	border: 3px solid var(--color-blue);
	animation: loaderAnim 0.7s linear infinite alternate forwards;
  opacity: 1;
  transition: opacity 0.45s ease-in-out;
}

.loading.op::before, 
.loading.op::after {
  opacity: 0;
}

@keyframes loaderAnim {
	to {
		transform: scale3d(0.5,0.5,1);
	}
}


.link__box {
  text-decoration: none;
  color: var(--color-dark);
  font-size: var(--font-title);
  font-weight: 500;
  font-family: "Libre Baskerville", serif;
  letter-spacing: 2px;
}

.num {
  display: inline;
  font-size: var(--font-small);
  font-weight: 500;
  vertical-align: super;
}

/* selected */
.selected{
  background-color: red;
}

/* to main */
.content-hidden { 
  pointer-events: none;
}