.section__about {
  width: 100%;
  height: fit-content;
  /* height: 100vh; */
  background-color: var(--color-bg);
  padding: 8rem var(--margin-h);
  color: var(--color-dark);
}

.about__box {
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  position: relative;
}

.about__text {
  text-align: justify;
  max-width: 600px;
  font-size: var(--font-text);
  font-weight: 400;
}

.contact {
  text-align: center;
  max-width: 600px;
  font-size: var(--font-text);
  font-weight: 400;
  padding-top: 1rem;
  line-height: 2rem;
}

.contact > a {
  text-decoration: none;
  color: var(--color-dark);
}
