.preview {
  width: 100%;
  position: fixed;
  height: 100%;
  z-index: 9;
  right: 0;
  top: 0;
  opacity: 0;
  overflow: scroll;
}

.preview__container {
  width: 100%;
  position: relative;
  height: fit-content;
  /* z-index: 9; */
  padding: 0 var(--margin-h);
  padding-top: calc(var(--margin-h) * 2);
  padding-bottom: calc(var(--margin-h) * 2);
  right: 0;
  top: 0;
  display: flex;
  flex-direction: column;
  align-items: center;
  /* justify-content: center; */
  gap: 2rem;
}

.preview__container > p {
  text-align: justify;
  max-width: 600px;
  font-size: var(--font-text);
  font-weight: 400;
  opacity: 0;
}

.preview__container .link__box {
  opacity: 0;
}

.preview__container > div {
  overflow: hidden;
}

.preview__subtitle{
  /* border: thin solid red; */
  display: block;
  font-size: var(--font-text);
  opacity: 0.5;
  overflow: hidden;
}
.preview__subtitle span{
  /* border: thin solid blue; */
  display: block;
  opacity: 0;
}

.link__image {
  height: 100%;
  filter: grayscale(0);
  transition: filter 500ms ease;
  /* width: 100%; */
  max-width: 600px;
}

@media (any-pointer:fine) {
  .link__image {
    height: 100%;
    filter: grayscale(100);
    transition: filter 500ms ease;
    /* width: 100%; */
    max-width: 600px;
  }

  .link__image:hover {
    filter: grayscale(0);
  }	
}

.link__image > img {
  width: 100%;
  opacity: 0;
}

